@import url(https://fonts.googleapis.com/css?family=Titillium+Web:200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.setValuesForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 25px;
}

.input {
  margin: 5px;
  height: 5vh;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--inputColor);
  font-family: "Titillium Web", sans-serif;
  font-size: 27px;
  color: var(--inputColor);
  text-align: center;
}

.nameOfTransaction {
  width: 20vw;
}

.cash::-webkit-input-placeholder, .nameOfTransaction::-webkit-input-placeholder {
  text-align: center;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  color: var(--inputColor);
  opacity: 0.5;
}

.cash::-moz-placeholder, .nameOfTransaction::-moz-placeholder {
  text-align: center;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  color: var(--inputColor);
  opacity: 0.5;
}

.cash:-ms-input-placeholder, .nameOfTransaction:-ms-input-placeholder {
  text-align: center;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  color: var(--inputColor);
  opacity: 0.5;
}

.cash::-ms-input-placeholder, .nameOfTransaction::-ms-input-placeholder {
  text-align: center;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  color: var(--inputColor);
  opacity: 0.5;
}

.cash::placeholder,
.nameOfTransaction::placeholder {
  text-align: center;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  color: var(--inputColor);
  opacity: 0.5;
}

.setConversionForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 25px;
}

.input {
  margin: 5px;
  height: 5vh;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--inputColor);
  font-family: "Titillium Web", sans-serif;
  font-size: 27px;
  color: var(--inputColor);
  text-align: center;
}

.cash {
  width: 7vw;
}

.setButton {
  background-color: transparent;
  border: 1px solid var(--lighterColor);
  width: 10vw;
  height: 7vh;
  font-family: "Titillium Web", sans-serif;
  color: var(--lighterColor);
  letter-spacing: 5px;
  font-size: 20px;
  margin: 10px;
  box-shadow: 3px 3px 3px 3px var(--shadowColor);
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.setButton:hover {
  color: var(--darkerColor);
  background-color: var(--lighterColor);
  border: 1px solid var(--darkerColor);
}

.conversion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 25px;
}

.zlotyConversion {
  font-size: 33px;
  color: var(--inputColor);
  font-weight: bold;
  margin: 5px;
}

.list {
  position: absolute;
  top: 20%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  list-style: none;
  text-align: center;
  font-size: 30px;
  height: 45vh;
  width: 90%;
  overflow: auto;
}

.list::-webkit-scrollbar {
  width: 2px;
  background-color: transparent;
}

.list::-webkit-scrollbar-thumb {
  background: var(--lighterColor);
}

.list::-webkit-scrollbar-corner {
  background: transparent;
}

.transactions {
  margin: 5px;
}

.deleteButton {
  background-color: transparent;
  border: 1px solid var(--lighterColor);
  width: 10vw;
  height: 7vh;
  font-family: "Titillium Web", sans-serif;
  color: var(--lighterColor);
  letter-spacing: 5px;
  font-size: 20px;
  margin: 10px;
  box-shadow: 3px 3px 3px 3px var(--shadowColor);
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.deleteButton:hover {
  color: var(--darkerColor);
  background-color: var(--lighterColor);
  border: 1px solid var(--darkerColor);
}

.numberOnList {
  font-size: 33px;
  color: var(--inputColor);
  font-weight: bold;
  margin: 5px;
}

.summaryBox {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 90%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.nameOfResult {
  font-size: 20px;
}

.result {
  font-size: 30px;
  font-weight: bold;
}

.theHighestBox {
  text-align: center;
  font-weight: normal;
  font-size: 25px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --darkerColor: hsla(201, 0%, 5%, 0.94);
  --lighterColor: hsla(201, 0%, 62%, 0.97);
  --shadowColor: hsla(195, 0%, 3%, 0.99);
  --inputColor: hsla(360, 0%, 82%, 0.83);
}

body {
  background-color: hsla(201, 0%, 5%, 0.94);
  background-color: var(--darkerColor);
  color: hsla(201, 0%, 62%, 0.97);
  color: var(--lighterColor);
  font-family: "Titillium Web", sans-serif;
  letter-spacing: 2px;
  text-shadow: 5px 5px 5px hsla(195, 0%, 3%, 0.99);
  text-shadow: 5px 5px 5px var(--shadowColor);
}

.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
}

.containerTransactions {
  height: 40%;
  width: 100%;
  border-bottom: 5px solid hsla(201, 0%, 62%, 0.97);
  border-bottom: 5px solid var(--lighterColor);
  box-shadow: 0 0 10px 10px hsla(195, 0%, 3%, 0.99);
  box-shadow: 0 0 10px 10px var(--shadowColor);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.formContainer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.containerList {
  height: 60%;
  flex-basis: 60%;
  border-right: 5px solid hsla(201, 0%, 62%, 0.97);
  border-right: 5px solid var(--lighterColor);
  box-shadow: 0px 20px 10px 10px hsla(195, 0%, 3%, 0.99);
  box-shadow: 0px 20px 10px 10px var(--shadowColor);
  position: relative;
}

.containerSummary {
  height: 60%;
  flex-grow: 1;
  position: relative;
}

.header {
  text-align: center;
  margin: 5px;
  font-size: 3em;
}

